.sidebar {
  width: 21vw;
  height: 100vh;
  position: fixed;
  z-index: 1;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.sidebar .content {
  background-color: burlywood;
  width: 20vw;
  height: 70%;
  border-radius: 10px;
  margin-top: 1vh;
  margin-left: 0.5vw;
}
.sidebar .content .menu {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.sidebar .content .menu span {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: aliceblue;
  height: 30px;
  width: 80%;
  border: black 1px solid;
  margin: 1px;
}
.sidebar .social-links {
  background-color: burlywood;
  width: 20vw;
  margin-top: 1vh;
  margin-left: 0.5vw;
  border-radius: 10px;
  height: 7%;
  margin-bottom: 10px;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
}/*# sourceMappingURL=style.css.map */